import React from 'react'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import Carousel from '../../../components/Carousel'
import PageModule from '../../../components/PageModule'
import LeftTitle from '../../../components/LeftTitle'
import { responsive } from '../../../components/ThemeProvider/theme'
import useResponsive from '../../../contexts/mediaQuery/useResponsive'

import circle from '../circle.svg'

const circleData = [
  {
    width: '18.4%',
    minWidth: '260px',
    top: '44.8%',
    right: '24.8%',
    titleText: (
        <>
          購票會員人數<Text.Inline color="dance">近20萬人</Text.Inline><br/>
          新註冊會員佔<Text.Inline color="dance">40%</Text.Inline>
        </>
    ),
    description: [
      '相較2019年持平',
    ]
  },
  {
    width: '18.4%',
    minWidth: '260px',
    top: '14.4%',
    right: '10.5%',
    titleText: (
      <>
        會員購票金額<br/>
        佔全年票房<Text.Inline color="dance">79%</Text.Inline>
      </>
    ),
    description: [
      '相較2019年(65%)明顯成長',
    ],
  },
  {
    width: '18.4%',
    minWidth: '260px',
    top: '60%',
    right: '4.3%',
    titleText: (
        <>
          年購票超過1次之會員<br/>
          佔整體會員<Text.Inline color="dance">30%</Text.Inline>
        </>
    ),
    description: [
      '相較2019年(35%)微幅下滑',
    ]
  },
]

const CircleModule = ({ width, top, right, titleText, description, ...props}) => (
  <Box.Absolute width={width} top={top} right={right} {...props}>
    <Image src={circle} />
    <Box.AbsCenter width="100%" textAlign="center">
      <Text.H4 whiteSpace="pre-wrap">
        {titleText}
      </Text.H4>
      <Box.Inline>
        {description.map((d, k) => (
          <Text fontSize={responsive('0.75em', '1em')} mt="0.625rem" whiteSpace="pre-wrap" fontWeight="400" textAlign={responsive('center', "left")} key={k}>
            {d}
          </Text>
        ))}
      </Box.Inline>
    </Box.AbsCenter>
  </Box.Absolute>
)

const Page = () => {
  const { isMobile } = useResponsive()
  return (
    <PageModule
      left={
        <LeftTitle
          smallTilte="2020疫情之下"
          title="觀眾持續力挺表演藝術市場"
          description="透過會員購票行為數據，可以看出即使在疫情影響下，觀眾仍未離開，並持續注入力量，活絡表演藝術市場。"
          note={!isMobile && '註1：無法探討未使用會員帳號購票之行為，如票口購買、藝文單位自售票券。\n註2：會員性別、年齡及居住地資料，依據消費者申請會員帳號時填寫資料進行統計，消費者是否為實際參與藝文活動的觀眾或期間可能變動個人資料，此部分存在潛在落差。'}
        />
      }
      right={
        <>
          {isMobile ? (
            <Carousel
              width="70%"
              mx="auto"
              slidesToShow={1}
            >
              {circleData.map(({ titleText, description, mobileDescription }, i) => (
                <CircleModule
                  titleText={titleText}
                  description={mobileDescription || description}
                  position="relative"
                  key={i}
                />
              ))}
            </Carousel>
          ) : circleData.map(({ width, top, right, titleText, description, minWidth }, i) => (
            <CircleModule
              width={width}
              minWidth={minWidth}
              top={top}
              right={right}
              titleText={titleText}
              description={description}
              key={i}
            />
          ))}
        </>
      }
    />
  )
}

export default Page
